<template>
     <v-container>
        <v-row justify="center">
            <v-card width="400">
                <v-card-title>JOIN US</v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-form ref="form" v-model="valid" lazy-validation>
                        <v-text-field v-model="user.email" label="EMAIL" :rules="rules" type="email"></v-text-field>
                        <v-text-field v-model="user.id" label="ID" :rules="rules"></v-text-field>
                        <v-text-field v-model="user.password" label="PASSWORD" type="password" @keypress.enter="signIn" :rules="rules"></v-text-field>
                    </v-form>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn text color="primary" @click="join">JOIN</v-btn>
                        <v-spacer></v-spacer>
                        <v-btn text color="primary" @click="goBack">BACK</v-btn>
                    </v-card-actions>
                </v-card-text>
            </v-card>
        </v-row>
    </v-container>
</template>
<script>
export default {
  components: {
  },
  props: {
    loginDialog: Boolean
  },
  data () {
    return {
      valid: false,
      user: {
        email: '',
        id: '',
        password: ''
      },
      rules: [
        v => !!v || 'text is required',
        v => (v && v.length <= 10) || 'text must be less than 10 characters',
        v => (v && v.length >= 2) || 'text must be less than 2 characters'
      ]
    }
  },
  methods: {

    async signIn (event) {
      // this.$http.post('/api/users/signUp', {
      this.$http.post('/router/index', {
        user: this.user
      })
        .then((res) => {
          if (res.data.success === true) {
            alert(res.data.message)
            this.$router.push('/login')
          }
          if (res.data.success === false) {
            alert(res.data.message)
          }
        })
        .catch(function (error) {
          // alert('error')
          console.log(error)
        })
    },
    join () {
      console.log('join')
    },
    goBack () {
      this.$router.push({ name: 'Login' })
    }
  }
}
</script>
<style scoped>
.v-text-field{
    margin: 0 auto;
    margin-bottom: 20px;
    width: 400px;
}
</style>
